// 
export const HOLIDAY_TIME_VALUE = "---"

export interface Holiday {
    value: number
    text: string
    isAllDayOff: boolean
}

export const UNPAID_HOLIDAY = { value: 6, text: "", isAllDayOff: true }

export const HOLIDAYS: Holiday[] = [
    { value: 1, text: "", isAllDayOff: true },
    { value: 2, text: "", isAllDayOff: false },
    { value: 3, text: "", isAllDayOff: false },
    { value: 4, text: "", isAllDayOff: true },
    { value: 5, text: "BC", isAllDayOff: true },
    UNPAID_HOLIDAY,
    { value: 7, text: "", isAllDayOff: true },
    { value: 8, text: "", isAllDayOff: true },
    { value: 9, text: "", isAllDayOff: true },
    { value: 10, text: "", isAllDayOff: true },
    { value: 11, text: "SP5", isAllDayOff: true },
    { value: 12, text: "", isAllDayOff: true },
    { value: 13, text: "", isAllDayOff: true },
    { value: 14, text: "", isAllDayOff: true },
    { value: 15, text: "", isAllDayOff: true },
]

export function getHolidayFromValue(value?: number): Holiday | undefined {
    if (value === undefined) {
        return undefined
    }

    return HOLIDAYS.find((h) => h.value === value)
}

export function getHolidayText(value?: number) {
    const holiday = getHolidayFromValue(value)
    if (holiday === undefined) {
        return ""
    }
    return holiday.text
}

export interface SpecialNoteHoliday {
    text: string
    requireInputUnpaid: boolean
}

export const SPECIAL_NOTES: SpecialNoteHoliday[] = [
    { text: "", requireInputUnpaid: true },
    { text: "", requireInputUnpaid: true },
    { text: "", requireInputUnpaid: true },
    { text: "", requireInputUnpaid: true },
    { text: "", requireInputUnpaid: false },
    { text: "", requireInputUnpaid: false },
    { text: "RF", requireInputUnpaid: false },
]

export function getSpecialNoteHolidayFromText(text?: string): SpecialNoteHoliday | undefined {
    if (text === undefined) {
        return undefined
    }

    return SPECIAL_NOTES.find((s) => s.text === text)
}



